import helper from '../../common/helper.js';
import uniSegmentedControl from "@/components/uni-segmented-control.vue"
export default {
	components: {
		uniSegmentedControl
	},
	data() {
		return {
			items: ['最新领取', '到店时间'],
			current: 0,
			rwlistArray: "", //任务列表数据
			Tishi: '',
			num: '9', //默认显示9条任务数据
			bottomtisi: '',
			rwid: '' //任务id
		}
	},
	onLoad(e) {
		this.requestTask()
		this.rwid = e.rwid;
	},
	onShow() {
		this.requestTask()
	},
	onPullDownRefresh: function() {
		this.requestTask(true)
		uni.showLoading({
			title: "刷新中....",
			mask: true
		})
	},
	//触底事件
	onReachBottom() {
		var tas1 = new String('5'); //触底加载5条数据
		this.num = Number(this.num) + Number(tas1);
		this.requestTask();
	},
	methods: {
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		// 请求任务列表
		requestTask(isdown) {
			helper.islogin_Mer(true);
			uni.showLoading({
				mask: true,
				title: '加载中'
			})
			uni.request({
				url: helper.websiteUrl + 'mtask/gettasklistbytime',
				method: 'GET',
				data: helper.postdata({
					"taskid": this.rwid,
					"page": '1',
					"num": this.num,
					"type": "3"
				}),
				success: (res) => {
					if (res.data.code == 200) {
						this.rwlistArray = res.data.data.baseinfo;
						res.data.data.baseinfo.forEach((e) => {
							e.Receivedate = e.Receivedate.substr(0, 10);
						});

					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
					if (res.data.data.baseinfo.length >= this.num) { //判断触底加载是否到底最后一条 否则提示没有更多数据了
						this.bottomtisi = '正在加载...'
					} else {
						this.bottomtisi = '没有更多数据了'
					}
					if (res.data.data.baseinfo.length === 0) { //判断分类数据是否为空 否则提示暂无任务
						this.Tishi = "暂无任务";
						this.bottomtisi = ''
					} else {
						this.Tishi = "";
					}
					uni.hideLoading();
				},
				fail() {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常，请下拉刷新'
					});
				},
				complete: () => {
					if (isdown) {
						uni.stopPullDownRefresh(); //刷新停止
					}
				}
			});
		}
	}
}
